body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


body {
  background-color: #fffde7;
}

.SallveText {
  color: #757575;
  font-size: 60px;
  font-family: fantasy;

}
nav div .form {
    height: 100%;
}
.Movie {
  color: #757575;
  font-size: 20px;
  text-align: left;
}
.SmallText {
  color: #757575;
  font-size: 10px;

}
.nav-wrapper {
  background-color:#ff8a65
}
.modal {
  display: none;
  position: fixed;
  left: 0;
  right: 0;
  background-color:#fffde7;
  padding: 0;
  max-height: 70%;
  width: 55%;
  margin: auto;
  overflow-y: auto;
  border-radius: 2px;
  will-change: top, opacity;
}
.modal .modal-footer {
  border-radius: 0 0 2px 2px;
  background-color: #ff8a65;
  padding: 4px 6px;
  height: 56px;
  width: 100%;
  text-align: right;
}
.ButtonColor {
  color: #fffde7
}
.text {
  color: #757575;
  font-size: 20px;
  text-align: center;
}
@media only screen and (max-width: 600px) {
  div.quebra {
    clear:both;
  }
}

